import React, { useRef } from 'react';
import SunEditor from 'suneditor-react';
import '../styles/RichTextEditor.css';

export function RichTextEditor({ onChange, initialValue }) {

    const editorOptions = {
        buttonList: [
            ['formatBlock', 'bold', 'underline', 'italic', 'strike'],
            ['fontColor', 'hiliteColor'],
            ['removeFormat'],
            ['align', 'horizontalRule', 'list'],
            ['table', 'link', 'image', 'video', 'audio'],
            ['fullScreen', 'codeView'],
        ],
        formats: [
            {
                tag: 'p',
                name: 'P√°rrafo'
            },
            {
                tag: 'h1',
                name: 'T√≠tulo'
            },
            {
                tag: 'h2',
                name: 'Subt√≠tulo'
            },
            {
                tag: 'h3',
                name: 'Secci√≥n'
            },
            {
                tag: 'h4',
                name: 'Subsecci√≥n'
            },
            {
                tag: 'blockquote',
                name: 'Bloque de descripci√≥n'
            },
            {
                tag: 'pre',
                name: 'Notas de DM'
            },
            //'p', 'blockquote', 'pre', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6'
        ]
    };

    const editor = useRef();

    let currentChangeId = 0;
    const changeUpdateTimeout = 200;

    const getSunEditorInstance = (sunEditor) => {
        editor.current = sunEditor;
    };

    const deferredUpdate = (changeId) => {
        setTimeout(() => {
            if (changeId === currentChangeId) {
                // Sometimes the ref does not have getContents function, in that case we skip this deferred update
                if (!editor.current.getContents) return;
                onChange(editor.current.getContents());
            }
        }, changeUpdateTimeout);
    }

    const handleInput = () => {
        currentChangeId += 1;
        deferredUpdate(currentChangeId);
    }

    const getEditorHeight = () => {
        const height = window.innerHeight
            || document.documentElement.clientHeight
            || document.body.clientHeight;
        if (height <= 640) return '48vh';
        if (height <= 900) return '67vh';
        return '73vh';
    }

    return <div className="EditorContainer">
        <SunEditor lang='es' defaultValue={initialValue} height={getEditorHeight()}
            onChange={(state) => handleInput()} onInput={(event) => handleInput()}
            getSunEditorInstance={getSunEditorInstance} setOptions={editorOptions} />
    </div >;
}